@use 'colors';

html,
body {
    box-sizing: border-box;
    // font-size: 10px;
    font-size: 100%; // 16 px is typically the default
    // font-size: 1.25vw;
    font-family: Helvetica, Arial, sans-serif;
    width: 100%;
    margin: 0;
}

@font-face {
    font-family: 'gsicons';
    src: url('fonts/gsicons.eot?kuic2q');
    src: url('fonts/gsicons.eot?kuic2q#iefix') format('embedded-opentype'),
        url('fonts/gsicons.ttf?kuic2q') format('truetype'),
        url('fonts/gsicons.woff?kuic2q') format('woff'),
        url('fonts/gsicons.svg?kuic2q#gsicons') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

i.gsi {
    font-family: 'gsicons' !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    display: inline-flex;
    align-items: center;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &.gsi-michelin-star:before {
        content: "\e901";
    }

    &.gsi-michelin-guide:before {
        content: "\e900";
        font-size: 1.2rem;
    }
}

