*,
*:before,
*:after {
    box-sizing: inherit;
}

.icons:hover {
    transition: all 0.35s ease-in-out;
}

a {
    text-decoration: none;
    cursor: pointer;

    &:active,
    &:visited,
    &:link {
        color: colors.$linkColor;
    }

    &:hover {
        color: colors.$hoverLinkColor;
        transition: all 0.35s ease-in-out;
    }
}

h3 {
    color: #333;
}

p {
    line-height: 140%;
}

.visible {
    opacity: 1 !important;
}

.invisible {
    opacity: 0 !important;
}

.hide {
    display: none !important;
}



button {
    position: relative;
    display: inline-block;
    border: 2px solid colors.$lightShadeGrey;
    border-radius: 0.4rem;
    text-align: center;
    color: #505050;
    background-color: colors.$white;

    &:focus {
        outline: none !important;
    }

    &:hover {
        background-color: colors.$hoverLinkColor;
        border-color: colors.$lightGrey;
        color: colors.$white;
        transition: all 0.5s ease-in-out;

        cursor: pointer;

        a {
            transition: color 0.4s;
            color: colors.$white;
        }
    }

    &.active {
        background-color: colors.$main;
        border-color: colors.$lightGrey;
        color: colors.$white;
        outline: none !important;
    }
}

blockquote {
    background: #e9e9e9;
    color: colors.$linkColor;
    border-left: 0.4rem solid #bbb;
    margin: 0;
    padding: 0.1rem 1rem;
}

code {
    color: colors.$main;
    background-color: rgba(27, 31, 35, .05);
    border-radius: .3rem;
    font-size: 85%;
    margin: 0;
    padding: .2em .4em;
}

// This is for both the sidebar and content panes
.metadata {
    display: inline-flex;
    align-items: center;
    flex-direction: row;
    align-content: center;
    flex-wrap: wrap;
}
