@use 'global';
@use 'colors';
@use 'header';
@use 'content';
@use 'sidebar';
@use 'search';

$global-margin-left: 4rem;

section.pagination {
    position: relative;
    margin-bottom: 5rem;
    padding-left: 4rem;
    text-align: center;

    @media only screen and (max-device-width: 768px) {
        padding-left: 0;
    }

    hr {
        border: 0;
        height: 2px;
        background: colors.$lightGrey;
        // background-image: linear-gradient(to right, #ccc, #333, #ccc);
        margin: 4rem 0 2rem;
    }

    .solo-button {
        width: 100% !important;
    }

    .left-button,
    .right-button {
        display: inline-block;
        text-align: center;

        a {
            display: inline-block;
            padding: 0.5rem 1.50rem;
            width: 100%;

            &:hover {
                background-color: transparent;
            }
        }
    }

    .right-button {
        // position: absolute;
        // right: 0;
    }
}

#main:after {
    content: "";
    display: block;
    clear: both;
}

footer {
    width: 100%;
    text-align: center;
    line-height: 4rem;
    margin-top: 4rem;

    display: inline-flex;
    align-content: center;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}