@use 'colors';

#content {
    &.search {

        .filters {
            margin-top: 1.4rem;

            div,
            select {
                font-size: 1rem;
                display: inline-block;
                margin-right: 0.4rem;
                margin-bottom: 0.8rem;

                padding: 0.1rem 0.4rem;
                border-radius: 0.6rem;
            }
        }

        .search-term {
            color: colors.$main;
            font-weight: bold;
        }

        .search-results {
            margin-top: 1.2rem;
            border-top: 1px solid colors.$lightGrey;

            .pagination-selector {
                display: flex;
                align-items: center;

                a {
                    margin: 0 4px;
                }

                a:hover {
                    text-decoration: underline;
                }

                a.active {
                    font-weight: bold;
                    font-size: 1.5rem;
                }
            }

            .stats {
                font-size: 1rem;
                margin-top: 1.2rem;
                font-style: italic;
            }

            section {
                margin: 2rem 0;
            }

            img {
                display: block;
                width: 12rem;
                float: left;
                margin-right: 1rem;
                margin-bottom: 0.4rem;
            }

            a {
                &.title {
                    font-size: 1.4rem;
                    display: flex;
                    line-height: 1.8rem;
                    margin-bottom: 0.5rem;
                }
            }

            .brief {
                font-size: 1rem;
                line-height: 120%;
                margin-top: 0.4rem;
            }
        }


        @media only screen and (max-device-width: 768px) {
            .filters {
                div:nth-child(5) {
                    // hack to target the locale form field in mobile
                    // margin-top: 2rem;
                }
            }
        }
    }
}
