@use 'colors';

#content {
    display: inline-block;
    width: 70%;

    font-size: 1rem;

    padding-top: 2rem;

    article {
        margin-left: 4rem;
        margin-bottom: 4rem;
        padding-right: 2rem;

        .attachments {
            font-size: 0.8rem;
            margin-top: 2rem;
            display: block;

            .title {
                border-radius: 0.5rem 0.5rem 0 0;
                background-color: #6AB0DE;
                color: #FFF;
                padding: 0 0.4rem;
                font-size: 0.8rem;
            }

            .files {
                padding: 0.8rem;
                background-color: #eee;
                border-radius: 0 0 0.5rem 0.5rem;
            }
        }

        .table-of-contents {
            font-size: 0.8rem;
            display: block;

            .title {
                border-radius: 0.5rem 0.5rem 0 0;
                background-color: #6AB0DE;
                color: #FFF;
                padding: 0 0.4rem;
                font-size: 0.8rem;
            }

            .items {
                padding: 0.4rem 0.8rem;
                background-color: #eee;
                border-radius: 0 0 0.5rem 0.5rem;

                ol {
                    margin: 0;
                }
            }
        }

        .title {
            font-size: 1.4rem;
            min-height: 2rem;
            line-height: 2rem;
            font-weight: 900;
            letter-spacing: 0.05rem;
            margin: 0;

            a {
                display: inline-flex;
                gap: 0.2rem;
                align-items: center;

                gsi.michelin-star {
                    color: colors.$michelin;

                    &:hover {
                        color: currentColor;
                    }
                }
            }
        }

        .posted {
            margin-top: 0.5rem;
            font-size: 0.7rem;
            letter-spacing: 1px;
            opacity: 0.6;
        }

        .summary {
            margin-top: 0.5rem;
            line-height: 140%;

            &.single {
                border-top: 1px dashed colors.$darkGrey;
            }

            a {
                font-size: inherit;
                font-weight: bold;
            }

            figure {
                margin: 0;

                img {
                    width: 100%;
                }
            }
        }

        .meta {
            display: inline-flex;
            font-size: 1rem;
            color: #4a5056;
            line-height: 150%;
            margin-right: 0.4rem;
            border-radius: 0.4rem;

            align-items: center;
            flex-direction: row;
            align-content: center;
            flex-wrap: wrap;

            .icons {
                line-height: 200%;

                &:hover {
                    color: colors.$hoverLinkColor;
                }

                a {
                    font-family: Helvetica, Arial, sans-serif;
                    font-weight: normal;
                    margin-left: 0.2rem;
                    display: inline-flex;
                }
            }


            a {
                text-transform: capitalize;
                letter-spacing: 0.05rem;
                line-height: 140%;
                vertical-align: middle;
                border: 1px solid #FFF;
                border-radius: 0.4rem;
                padding: 0.1rem 0.2rem;

                &:hover {
                    color: colors.$hoverLinkColor !important;
                    border-color: colors.$hoverLinkColor;
                }
            }
        }

        .images {
            border-top: .4rem solid colors.$lightGrey;
        }

        .read-more {
            margin-top: 1rem;

            a {
                display: inline-block;
                padding: .25rem .5rem;
                width: 100%;
                line-height: 1.5rem;
            }
        }

        &.stats {

            &.clear::after {
                content: '';
                clear: both;
                display: table;
            }

            .filters {
                float: left;
                width: 30%;
            }

            .results {
                padding-left: 30%;

                .second-level ul {
                    display:inline-flex;
                    flex-wrap: wrap;
                    row-gap: 0.4rem;
                }
            }

            ul {
                list-style-type: none;
                margin: 0;
                padding-left: 0;
            }

            #cuisine-breakdown {
                margin-top: 2rem;
            }

            button {
                padding: 0.2rem 0.5rem;
                background-color: colors.$statsButtonBackground;
                border-color: colors.$lightWhiteGrey;
                opacity: 1;

                &:hover {
                    color: colors.$black;
                    border-color: colors.$darkGrey;
                }

                &.active {
                    background-color: colors.$main;
                    // background-color: colors.$white;
                    border-color: colors.$darkGrey;
                    color: colors.$white;
                    opacity: 1;
                }

                &:active {
                    transform: scale(0.97);
                    transition: all 0.1s ease-in;
                }
            }

            .sub-title {
                margin: 2rem 0 0.5rem;
                font-size: 1.2rem;
                min-height: 1.8rem;
                line-height: 1.8rem;
                font-weight: 500;
                letter-spacing: 0.1rem;
                display: flex;
                column-gap: 1rem;

                button {
                    font-size: 1rem;
                }
            }

            .locale-filters,
            .cuisine-filters {
                display: flex;
                flex-wrap: wrap;
                gap: 0.25rem;
                padding: 1rem;
                border: 2px solid colors.$lightGrey;
                border-radius: 1rem;
            }

            ul.buttons {
                display: inline-flex;
                flex-wrap: wrap;
                gap: 0.25rem;
                margin-top: 0.5rem;
            }

            .synopsis {
                margin-bottom: 2rem;
            }

            .first-level {
                margin-top: 2rem;
            }

            .neighborhood-level {
                margin-top: 1rem;
                margin-left: 2rem;
            }

            .second-level {
                margin-left: 2rem;
                margin-top: 0.4rem;

                a {
                    margin-right: 1rem;
                }
            }

            .third-level {
                margin-left: 4rem;
                margin-top: 0.4rem;
            }
        }

        .cse .gsc-control-cse,
        .gsc-control-cse {
            padding: 0;
        }
    }


    @media only screen and (max-device-width: 768px) {
        width: 90%;
        margin: auto;
        display: block;
        font-size: 1.2rem;
        // padding-left: 0;
        // border-top: 1px solid $main;

        padding-right: 0;

        article {
            margin-left: 0;
            padding-right: 0;

            &.stats {
                .filters {
                    float:none;
                    width: 100%;
                }

                .results {
                    padding-left: 0;
                    width: 100%;
                }
            }

            .posted {
                display: block;
            }
        }
    }
}
