@use 'colors';

#sidebar {
    vertical-align: top;
    float: right;
    width: 30%;
    min-width: 30%;
    border-left: 2px solid colors.$lightGrey;
    opacity: 1;
    // transition: all 0.1s;

    @media only screen and (max-device-width: 768px) {
        float: none;
        width: 80%;
        margin: auto;
        padding-left: 0;
        padding-right: 0;
        border: 0;
        border-top: 2px solid colors.$black;
    }

    .tags {
        position: relative;
        margin-bottom: 1.5rem;

        .tag {
            ul {
                list-style-type: none;
                margin-left: 1rem;
                padding: 0;
                font-size: 0.9rem;

                li {
                    display: inline-block;
                    margin: 0.4rem 0.4rem 0 0;
                    padding: 0.1rem 0.2rem;
                    border: 1px solid #FFF;
                    border-radius: 0.4rem;

                    &:hover {
                        border-color: colors.$hoverLinkColor;
                    }
                }
            }
        }
    }

    .group {
        border-bottom: 2px solid colors.$lightGrey;

        .group-item {
            margin: 1rem;
        }

        section.title {
            /** Title at the top of a group **/
            font-size: 1.4rem;
            line-height: 2.5rem;
            font-weight: 800;
            letter-spacing: 0.2rem;
            border-bottom: 2px solid colors.$lightGrey;
            background-color: #F0F0F0;

            margin-bottom: 1rem;
            color: #232323;

            div {
                margin-left: 1rem;
            }
        }
    }

    .posts {
        position: relative;

        .post {
            display: block;
            margin-left: 1rem;
            margin-bottom: 1.4rem;
            padding-right: 1rem;

            .title a {
                font-size: 1.2rem;
                font-weight: 800;

                display: inline-flex;
                gap: 0.2rem;
                align-items: center;
            }

            .last-modified,
            .posted {
                font-size: 0.6rem;
                margin: 0.2rem 0 0.1rem;
                opacity: 0.6;
                letter-spacing: 1px;
            }

            .meta {
                line-height: 150%;
                font-size: 0.9rem;
                margin-right: 0.4rem;

                display: inline-flex;
                flex-direction: row;
                align-items: center;
                flex-wrap: wrap;

                .icons {

                    line-height: 150%;
                    display: inline-flex;
                    flex-wrap: wrap;
                    align-items: center;

                    &:hover {
                        color: colors.$hoverLinkColor;
                    }

                    a {
                        font-family: Helvetica, Arial, sans-serif;
                        font-weight: normal;
                        margin-left: 0.2rem;
                    }
                }

                a {
                    padding: 0.1rem 0.2rem;
                    border-radius: 0.4rem;
                    border: 1px solid colors.$white;

                    &:hover {
                        color: colors.$hoverLinkColor;
                        border-color: colors.$hoverLinkColor;
                    }
                }
            }
        }
    }
}
