@use 'colors';

// 8, 16, 24, 32, 48, 64, 95

$header-height: 10rem; // 16 px is global CSS default.
$header-line-height: 10rem;

$border-thickness: 0.2rem;

header {
    width: 100%;
    position: relative;
    margin-left: auto;
    margin-right: auto;

    // FIXME: temporary
    padding-top: 2rem;

    .logo {
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        height: $header-height;
        width: 20rem;
        line-height: $header-line-height;
        margin-left: auto;
        margin-right: auto;
        cursor: pointer;

        background-image: url('img/logo.png');
    }

    @media only screen and (max-device-width: 768px) {
        height: auto;
        width: 100%;

        .logo {
            height: 10rem;
            width: 100%;
            margin-left: 0;
            margin-right: 0;
            background-position-x: center;
        }
    }
}

#navigation {
    font-size: 1.2rem;
    text-align: center;
    margin-top: 2rem;

    border-top: $border-thickness solid colors.$black;
    border-bottom: $border-thickness solid colors.$black;
    padding: 1rem 0;
    width: 100%;
    display: inline-flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 1rem;
    align-items: center;

    section {

        margin: 0 1.2rem;

        div {
            display: inline-block;
        }

        &.map {
            .icons:hover {
                color: colors.$hoverLinkColor;
            }

            div.map-group {
                padding: 0.1rem 0.4rem;
                border: 1px solid #FFF;
                border-radius: 0.6rem;
                margin-left: .2rem;

                &:hover {
                    border: 1px solid colors.$hoverLinkColor;
                }
            }

            a {
                font-family: Helvetica, Arial, sans-serif;
                font-weight: 500;
            }
        }

        &.search {
            position: relative;
            display: inline-block;

            .fa-search.icons:hover {
                color: colors.$hoverLinkColor;
            }

            div.search-group {
                padding: 0.1rem 0.4rem;
                border: 1px solid #FFF;
                border-radius: 0.6rem;
                margin-left: .2rem;

                &:hover {
                    border: 1px solid colors.$hoverLinkColor;
                }
            }

            a {
                font-family: Helvetica, Arial, sans-serif;
                font-weight: 500;
            }

            form {
                display: flex;
                border: $border-thickness solid colors.$black;
                border-radius: 0.4rem;

                position: absolute;
                right: 0;
                top: 2rem;
                z-index: 100;

                input {
                    text-indent: 1rem;
                    height: 2.5rem;
                    font-size: 120%;
                    border: 0;
                    border-radius: 0.4rem;

                    &:focus {
                        outline: none;
                    }
                }

                button {
                    border: 0;
                    border-radius: 0 0.2rem 0.2rem 0;
                    padding: 0 1rem;

                    &:hover {
                        color: colors.$white;
                        transition: color 0.25s ease-in-out, background-color 0.5s ease-in-out;
                    }
                }
            }
        }

        &.locale {
            display: inline-block;
            position: relative;
            // margin-right: 1.2rem;

            .icons:hover {
                color: colors.$hoverLinkColor;
            }

            div.locale-group {
                padding: 0.1rem 0.4rem;
                border: 1px solid colors.$white;
                border-radius: 0.6rem;
                margin-left: 0.2rem;

                font-family: Helvetica, Arial, sans-serif;
                font-weight: 500;

                &:hover {
                    border: 1px solid colors.$hoverLinkColor;

                    a {
                        color: colors.$hoverLinkColor;
                    }
                }
            }

            #locale-text {
                margin-right: 0.4rem;
            }

            .locale-picker {
                // padding: 0.1rem 0.4rem;
                // border: 1px solid #FFF;
                // border-radius: 1rem;

                &:hover,
                &.active {
                    // border-color: $black;
                    color: colors.$hoverLinkColor;
                    cursor: pointer;
                }
            }

            #locale-dropdown {
                left: 0;
                border: 1px solid colors.$black;
                background-color: colors.$white;
                position: absolute;
                border-radius: 0.8em;
                display: inline-block;
                overflow: hidden;
                width: 10rem;
                z-index: 1;
                margin-top: 0.5em;
                text-align: left;

                font-family: Helvetica, Arial, sans-serif;
                font-weight: 500;
                color: colors.$black;

                .option {
                    display: block;
                    font-size: 1rem;
                    padding: 0.75rem 0 0.75rem 1rem;
                    margin: 0;

                    &:hover {
                        background-color: colors.$hoverLinkColor;
                        color: colors.$white;
                        cursor: pointer;
                        transition: color 0.25s ease-in-out, background-color 0.5s ease-in-out;
                    }
                }
            }
        }
    }


    @media only screen and (max-device-width: 768px) {
        position: relative;

        div.locale {
            position: relative;

            #locale-dropdown {
                min-width: 15rem;
                width: 100%;
            }
        }

        section {

            &.search form {
                position: relative;
                top: 0.25rem;
                right: auto;

                input {
                    width: 10rem;
                }
            }
        }
    }
}
